.botonNext {
  --tw-border-opacity: 1;
  border-color: rgba(255, 255, 255, var(--tw-border-opacity));
  border-right-width: 2px;
}

.botonPrev {
  --tw-border-opacity: 1;
  border-color: rgba(255, 255, 255, var(--tw-border-opacity));
  border-right-width: 2px;
  transform: scaleX(-1);
}

.resultadoImg {
  width: 160px;
  height: 160px;
}

.resultadoPlay {
  transition: all 300ms ease;
}

.resultadoAlbum:hover .resultadoPlay {
  opacity: 1;
}

.reproductorContenedor {
  grid-template-columns: 1fr 2fr 6fr 3fr;
  --tw-bg-opacity: 1;
  background-color: rgba(232, 96, 96, var(--tw-bg-opacity));
  height: 11rem;
  padding-left: 1.25rem;
  padding-right: 1.25rem;
  padding-top: 0.75rem;
  position: fixed;
  bottom: 0px;
  width: 100%;
}

@media (min-width: 1024px) {
  .reproductorContenedor {
    display: grid;
    height: 6rem;
    padding-left: 0px;
    padding-right: 0px;
    padding-top: 0px;
  }
}

input[type='range'] {
  width: 100px;
  background-color: transparent;
  -webkit-appearance: none;
}

input[type='range']:focus {
  outline: none;
}

input[type='range']::-webkit-slider-runnable-track {
  background: white;
  width: 100px;
  border-radius: 50px;
  height: 6px;
  cursor: pointer;
}

input[type='range']::-webkit-slider-thumb {
  margin: -7px 0;
  width: 20px;
  height: 20px;
  background: white;
  border-radius: 100%;
  cursor: pointer;
  -webkit-appearance: none;
}

input[type='range']:focus::-webkit-slider-runnable-track {
  background: white;
}

input[type='range']::-moz-range-track {
  background: white;
  width: 100px;
  border-radius: 50px;
  height: 6px;
  cursor: pointer;
}

input[type='range']::-moz-range-thumb {
  margin: -7px 0;
  width: 20px;
  height: 20px;
  background: white;
  border-radius: 100%;
  cursor: pointer;
}

input[type='range']::-ms-track {
  background: white;
  width: 100px;
  border-radius: 50px;
  height: 6px;
  cursor: pointer;
}

input[type='range']::-ms-fill-lower {
  background: white;
  border-radius: 2.6px;
}

input[type='range']::-ms-fill-upper {
  background: white;
  border-radius: 2.6px;
}

input[type='range']::-ms-thumb {
  margin: -7px 0;
  width: 20px;
  height: 20px;
  background: white;
  border-radius: 100%;
  cursor: pointer;
}

input[type='range']:focus::-ms-fill-lower {
  background: white;
}

input[type='range']:focus::-ms-fill-upper {
  background: white;
}

@supports (-ms-ime-align: auto) {
  input[type='range'] {
    margin: 0;
  }
}

